import type { Root } from 'react-dom/client';
import browser from 'webextension-polyfill';

export default function handleCleaning(root: Root) {
  const clean = () => {
    if (browser.runtime?.id) return;
    root.unmount();
    window.removeEventListener('CLEAN_PREV_CONTENT', clean);
  };

  window.dispatchEvent(new Event('CLEAN_PREV_CONTENT'));
  window.addEventListener('CLEAN_PREV_CONTENT', clean);
}
