import camelcaseKeys from 'camelcase-keys';
import { defaultTags, defaultUnlockedPlantIds } from 'const';
import { Map as V1StorageMap } from 'storage/v1/types';
import browser from 'webextension-polyfill';
import { Map, PlantRecord, SyncStatus } from './types';

export const migrateToV2 = async () => {
  const v1 = (await browser.storage.local.get(null)) as Partial<V1StorageMap>;
  const plantRecords: PlantRecord[] = (v1.grownTree || []).map(
    ({ plant, syncState }) => ({
      syncStatus: (syncState === 'Success'
        ? 'Succeeded'
        : 'Failed') as SyncStatus,
      plant: {
        ...camelcaseKeys(plant, { deep: true }),
        treeTypeGid: plant.trees[0]?.tree_type || 0,
      },
    })
  );

  const newData: Map = {
    version: 2,
    allowList: v1.whiteList || [],
    blockList: v1.blackList || [],
    growingPlant: v1.planting
      ? {
          ...camelcaseKeys(v1.planting.plant, { deep: true }),
          treeTypeGid: v1.planting.plant.trees[0]?.tree_type || 1,
        }
      : null,
    resultPlant: null,
    plantRecords,
    siteBlockingMode: v1.isWhitelistMode ? 'Allow' : 'Block',
    user: v1.user
      ? {
          id: v1.user.user_id,
          name: v1.user.user_name,
          avatarUrl: v1.profile?.avatar_url || null,
          healthTreeCount: v1.profile?.health_count || 0,
          deadTreeCount: v1.profile?.death_count || 0,
          coin: v1.coin || 0,
          boostEndTime: null,
          boostStartTime: null,
          boostRate: null,
          rememberToken: v1.user.remember_token,
        }
      : null,
    serverRegion: 'Global',
    tags: v1.tag ? camelcaseKeys(v1.tag, { deep: true }) : defaultTags,
    unlockedPlantIds: v1.purchasedProducts
      ? v1.purchasedProducts.map(({ gid }) => gid)
      : defaultUnlockedPlantIds,
    selectedPlantIndex: v1.currentTreeIndex || 0,
    selectedPlantingDuration: v1.lastTreeTime || 600,
    canShowNoAccountCTA: true,
    lastSyncedAt: new Date('2013-01-01').toISOString(),
    isInChinaMigration: false,
  };

  await browser.storage.local.set(newData);
  await browser.storage.local.remove([
    'currentTreeIndex',
    'coin',
    'DefaultTAG',
    'IDtemp',
    'blackList',
    'whiteList',
    'isWhitelistMode',
    'lastTreeTagId',
    'lastTreeTime',
    'lastTreeType',
    'lastUpdateTime',
    'grownTree',
    'tempNote',
    'tempTag',
    'tag',
    'purchasedProducts',
    'profile',
  ]);
};

export const migrateToV2_1 = async () => {
  const v2 = (await browser.storage.local.get(null)) as Map;
  const data: Partial<Map> = {
    version: 2.1,
    unlockedPlantIds:
      v2.unlockedPlantIds.length < defaultUnlockedPlantIds.length
        ? defaultUnlockedPlantIds
        : v2.unlockedPlantIds,
  };
  await browser.storage.local.set(data);
};
