export default function getExtShadowDOM() {
  const hostId = 'forest-ext-shadow-host';
  const host =
    document.querySelector(`#${hostId}`) ||
    (() => {
      const host = document.createElement('div');
      host.id = hostId;
      document.body.appendChild(host);
      return host;
    })();

  const create = () => {
    // Throw exception if shadow root exsist
    const shadowRoot = host.attachShadow({ mode: 'open' });
    const head = document.createElement('head');
    const appRoot = document.createElement('div');
    appRoot.id = 'root';
    shadowRoot.appendChild(head);
    shadowRoot.appendChild(appRoot);
    return [head, appRoot] as const;
  };

  try {
    return create();
  } catch {
    const { shadowRoot } = host;
    if (shadowRoot === null) return create();
    const head =
      shadowRoot.querySelector('head') ||
      (() => {
        const head = document.createElement('head');
        shadowRoot.appendChild(head);
        return head;
      })();
    const root =
      shadowRoot.querySelector('#root') ||
      (() => {
        const root = document.createElement('div');
        root.id = 'root';
        shadowRoot.appendChild(root);
        return root;
      })();
    return [head, root] as const;
  }
}
