import { Map as V2Map } from 'storage/v2/types';
import browser from 'webextension-polyfill';
import { Map, Tag } from './types';

export const migrateToV3 = async () => {
  const v2 = (await browser.storage.local.get(null)) as V2Map;
  const data: Partial<Map> = {
    version: 3,
    // Add property `deleted` to tags if not exists in tag
    // (propabaly default tags)
    tags: v2.tags.map((tag) =>
      (tag as Tag).deleted === undefined
        ? { ...tag, deleted: false }
        : (tag as Tag)
    ),
  };
  await browser.storage.local.set(data);
};
