import { SpringValue } from '@react-spring/web';
import React from 'react';
import { t } from 'utils';
import * as S from './styles';

type Props = {
  style: { opacity: SpringValue<number>; transform: SpringValue<string> };
  confirm: () => void;
  close: () => void;
};

export default function GiveUpDialog({ style, confirm, close }: Props) {
  return (
    <S.Overlay style={{ opacity: style.opacity }}>
      <S.Dialog style={{ transform: style.transform }}>
        <S.Title>{t('overlay_popup_title')}</S.Title>
        <S.Content>{t('overlay_popup_message')}</S.Content>
        <S.Buttons>
          <S.BtnCancel onClick={close}>{t('settings_edit_cancel')}</S.BtnCancel>
          <S.BtnDanger onClick={confirm}>{t('overlay_giveup')}</S.BtnDanger>
        </S.Buttons>
      </S.Dialog>
    </S.Overlay>
  );
}
