import browser from 'webextension-polyfill';
import { migrateToV2, migrateToV2_1 } from './v2';
import { migrateToV3 } from './v3';
import { Map } from './v3/types';

type StorageMap = Map;
type StorageKey = keyof Map;

export const get = async <Key extends StorageKey>(keys: Key[]) => {
  const data = await browser.storage.local.get(keys);
  return data as {
    [K in Key]: StorageMap[K];
  };
};

export const set = async <Key extends StorageKey>(record: {
  [K in Key]: StorageMap[K];
}) => {
  await browser.storage.local.set(record);
};

export const getData = async <Key extends StorageKey>(key: Key) => {
  const data = await browser.storage.local.get({ [key]: null });
  return data[key] as StorageMap[Key];
};

export const setData = async <Key extends StorageKey>(
  key: Key,
  setterOrData: ((data: StorageMap[Key]) => StorageMap[Key]) | StorageMap[Key]
) => {
  if (typeof setterOrData === 'function') {
    const data = setterOrData(await getData(key));
    return await browser.storage.local.set({ [key]: data });
  }

  return await browser.storage.local.set({ [key]: setterOrData });
};

export const migrate = async () => {
  const version = (await getData('version')) || 1;
  if (version < 2) await migrateToV2();
  if (version < 2.1) await migrateToV2_1();
  if (version < 3) await migrateToV3();
};
