import { enUS, zhCN, zhTW } from 'date-fns/locale';
import * as tldts from 'tldts';
import { L10nKey } from 'types';
import browser from 'webextension-polyfill';
import type { Message } from './messages';

export async function sendTabsMessage<T extends Message['type']>(message: {
  type: T;
  payload: Extract<Message, { type: T }>['payload'];
}) {
  const tabs = await browser.tabs.query({ url: ['https://*/*', 'http://*/*'] });
  await Promise.all(
    tabs.map(({ id = null }) =>
      id === null
        ? Promise.resolve()
        : browser.tabs.sendMessage(id, message).catch(() => {})
    )
  );
}

export async function sendMessage<T extends Message['type']>(message: {
  type: T;
  payload: Extract<Message, { type: T }>['payload'];
}): Promise<Extract<Message, { type: T }>['response']> {
  return await browser.runtime.sendMessage(message).catch(() => {});
}

export function postMessage<T extends Message['type']>(
  port: browser.Runtime.Port | null,
  message: {
    type: T;
    payload: Extract<Message, { type: T }>['payload'];
  }
): void {
  return port?.postMessage(message);
}

export const secondsToTime = (seconds: number) => {
  const sec = (seconds % 60).toString().padStart(2, '0');
  const min = Math.floor(seconds / 60).toString();
  return `${min}:${sec}`;
};

export const calcTreesCount = (plantDuration: number) => {
  return Math.min(4, Math.max(1, Math.floor(plantDuration / 1800))) as
    | 1
    | 2
    | 3
    | 4;
};

export const getLang = () => {
  const lang = browser.i18n.getUILanguage().toLowerCase();

  if (lang === 'zh-tw' || lang === 'zh-hk' || lang.includes('zh-hant')) {
    return 'zh_TW';
  }

  if (lang === 'zh-cn' || lang.includes('zh-hans')) {
    return 'zh_CN';
  }

  return 'en';
};

export const getDateLocale = () => {
  const lang = getLang();
  switch (lang) {
    case 'en':
      return enUS;
    case 'zh_TW':
      return zhTW;
    case 'zh_CN':
      return zhCN;
  }
};

export const unifyUrl = (url: URL) => {
  return `${url.hostname}${url.pathname}`.replace(/\/$/, '');
};

export const getCurrentTabUrl = async () => {
  const [tab] = await browser.tabs.query({
    currentWindow: true,
    active: true,
  });

  if (!tab?.url) return '';
  const { domain, hostname } = tldts.parse(tab?.url);
  if (domain === null) return '';
  return hostname!;
};

export const t = (key: L10nKey) => browser.i18n.getMessage(key);
