import { useTransition } from '@react-spring/web';
import { Port } from 'const';
import useDialogTrans from 'hooks/useDialogTrans';
import type { Message } from 'messages';
import React, { useEffect, useState } from 'react';
import { createRoot } from 'react-dom/client';
import * as storage from 'storage';
import { StyleSheetManager } from 'styled-components';
import { ContentProps } from 'types';
import { postMessage, secondsToTime, sendMessage, t } from 'utils';
import browser from 'webextension-polyfill';
import handleCleaning from './cleaner';
import GiveUpDialog from './components/GiveUpDialog';
import getExtShadowDOM from './shadow-dom';
import * as S from './styles';

type Props = {
  initialProps: ContentProps;
  port: browser.Runtime.Port;
};

const Content = ({ initialProps, port }: Props) => {
  const [canShow, setCanShow] = useState(false);
  const contentTrans = useTransition(canShow, {
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 },
  });
  const giveUpDialog = useDialogTrans();
  const [timeRemaining, setTimeRemaining] = useState(
    initialProps.timeRemaining
  );
  const [plantingStatus, setPlantingStatus] = useState(
    initialProps.plantingStatus
  );
  const [siteBlockingMode, setSiteBlockingMode] = useState(
    initialProps.siteBlockingMode
  );
  const [plant, setPlant] = useState(initialProps.plant);

  useEffect(() => {
    (async () => {
      if (plantingStatus !== 'Started') return setCanShow(false);
      switch (siteBlockingMode) {
        case 'Block': {
          const blockList = await storage.getData('blockList');
          return setCanShow(
            blockList.some((url) => location.href.includes(url))
          );
        }
        case 'Allow': {
          const allowList = await storage.getData('allowList');
          return setCanShow(
            allowList.some((url) => location.href.includes(url)) === false
          );
        }
      }
    })();
  }, [plantingStatus, siteBlockingMode]);

  useEffect(() => {
    const onPortMessage = (message: Message) => {
      switch (message.type) {
        case 'UPDATE_PLANT_AND_TIME_REMAINING':
          setTimeRemaining(message.payload.timeRemaining);
          setPlant(message.payload.plant);
          return;
        case 'UPDATE_PLANT':
          return setPlant(message.payload);
      }
    };
    const onMessage = (message: Message) => {
      switch (message.type) {
        case 'UPDATE_PLANTING_STATUS':
          return setPlantingStatus(message.payload);
        case 'UPDATE_CONTENT_SCRIPT_SITE_BLOCKING_MODE':
          return setSiteBlockingMode(message.payload);
      }
    };
    port.onMessage.addListener(onPortMessage);
    browser.runtime.onMessage.addListener(onMessage);
    return () => {
      port.onMessage.removeListener(onPortMessage);
      browser.runtime.onMessage.removeListener(onMessage);
    };
  }, []);

  const confirmGiveUp = () => {
    giveUpDialog.off();
    postMessage(port, { type: 'GIVE_UP', payload: null });
  };

  return contentTrans((style, canShow) =>
    canShow ? (
      <>
        <S.GlobalStyle />
        <S.Overlay style={style}>
          <S.Content>
            <S.Title>{t('overlay_tree_still_growing')}</S.Title>
            <S.PlantBall>
              <S.Plant src={browser.runtime.getURL(plant.image)} />
            </S.PlantBall>
            <S.CountDown> {secondsToTime(timeRemaining)} </S.CountDown>
            <S.GiveUpButton onClick={giveUpDialog.on}>
              {t('overlay_giveup')}
            </S.GiveUpButton>
            {giveUpDialog.trans(
              (style, canShow) =>
                canShow && (
                  <GiveUpDialog
                    style={style}
                    confirm={confirmGiveUp}
                    close={giveUpDialog.off}
                  />
                )
            )}
          </S.Content>
        </S.Overlay>
      </>
    ) : null
  );
};

(async function start() {
  const [head, rootContainer] = getExtShadowDOM();
  const root = createRoot(rootContainer);
  handleCleaning(root);
  const port = browser.runtime.connect({ name: Port.Content });
  const props = await sendMessage({ type: 'GET_CONTENT_PROPS', payload: null });
  root.render(
    <StyleSheetManager target={head}>
      <Content initialProps={props} port={port} />
    </StyleSheetManager>
  );
})();
